import React, { Component } from "react";
import {
  CCard,
  CCardBody,
  CButton,
  CModal,
  CModalHeader,
  CModalFooter,
  CModalTitle,
  CModalBody,
  CFormLabel,
  CFormInput,
  CForm,
  CBadge,
  CInputGroup,
  CFormSelect,
  CCol,
  CRow,
} from "@coreui/react-pro";
import {
  faSave,
  faEdit,
  faTrash,
  faPlusCircle,
  faTimesCircle,
  faQuestion,
} from "@fortawesome/free-solid-svg-icons";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import MaterialTable from "material-table";
import EDU_AXIOS from "src/helpers/edu_axios";
import notify from "src/helpers/notify";
import { Formik, Form, Field, ErrorMessage } from "formik";
import * as Yup from "yup";
import EVFormikControl from "src/ev_formik_controls/EVFormikControl";
import Loader from "src/loader/Loader";
let nature = [
  { text: "Main Subject", value: "main" },
  { text: "Optional Subject", value: "optional" },
  { text: "Practical Subject", value: "practical" },
  { text: "Elective-1", value: "elective1" },
  { text: "Elective-2", value: "elective2" },
];

class all_subjects extends Component {
  constructor(props) {
    super(props);

    this.state = {
      subjects: [],
      modalVisibilityAddSubject: false,
      modalVisibilityEditSubject: false,
      subject_name: "",
      subject_type: "",
      errorsForAddSubject: {},
      errorsForEditSubject: {},
      errorsForEditSubjectCode: {},
      subject_deleted: null,
      loggedInUserId: JSON.parse(localStorage.getItem("user")).uid,
      Edited_ShortCode: "",
      BlockSubject: "",
      subject_id: "",
    };

    this.handleAddSubjectSubmit = this.handleAddSubjectSubmit.bind(this);
  }
  componentWillMount() {
    // render data into table
    this.getSubjects();
  }

  // get all subjects list
  getSubjects = () => {
    let url = process.env.REACT_APP_API_PATH + "/sm/get-subjects/";
    EDU_AXIOS.get(url)
      .then((resp) => {
        this.setState({ subjects: resp.data.data });
      })
      .catch((error) => {
        console.log(error);
      });
  };

  // validation for add subject field
  formValidationAddSubject = () => {
    const { subject_name } = this.state;
    let isValid = true;
    const errorsForAddSubject = {};
    if (!/^[A-Za-z]+$/.test(subject_name)) {
      errorsForAddSubject.Subjectrequired = "Invalid Subject Name";
      isValid = false;
    }

    this.setState({ errorsForAddSubject });
    return isValid;
  };

  // validation for add subject field
  formValidationAddSubject = () => {
    const { subject_name } = this.state;
    let isValid = true;
    const errorsForAddSubject = {};
    /*if (!/^[A-Za-z]+$/.test(subject_name)) {
      errorsForAddSubject.Subjectrequired = "Invalid Subject Name";
      isValid = false;
    }*/
    if (!subject_name.trim()) {
      errorsForAddSubject.Subjectrequired = " Required";
      isValid = false;
    }

    this.setState({ errorsForAddSubject });
    return isValid;
  };

  // on click function + validation impose + Axios data
  handleAddSubjectSubmit(event) {
    event.preventDefault();
    const isValid = this.formValidationAddSubject();
    if (isValid) {
      // Validation Check

      let subject = this.state.subject_name;
      let created_by = this.state.loggedInUserId;
      let created_for = "global";
      let self = this;

      EDU_AXIOS.post(process.env.REACT_APP_API_PATH + "/sm/create-subject", {
        subject,
        created_by,
        created_for,
      })
        .then((res) => {
          self.getSubjects();
          self.setState({ modalVisibilityAddSubject: false });
          notify("success", res.data.message);
        })
        .catch((error) => {
          console.log(error);
        });
    }
  }

  // formik params
  initialValues = {
    subject_name: "",
    subject_type: "",
  };
  // validation rules of the form fields
  validationSchema = Yup.object().shape({
    subject_name: Yup.string().required("Subject name is required"),
    subject_type: Yup.string().required("Subject type is required"),
  });
  // submit form
  onSubmit = (values, { resetForm }) => {
    let subject = values.subject_name;
    let subject_type = values.subject_type;
    let created_by = this.state.loggedInUserId;
    let created_for = "global";
    let self = this;
    EDU_AXIOS.post(process.env.REACT_APP_API_PATH + "/sm/create-subject", {
      subject,
      subject_type,
      created_by,
      created_for,
    })
      .then((res) => {
        self.getSubjects();
        self.setState({ modalVisibilityAddSubject: false });
        resetForm({ values: "" });
        notify("success", res.data.message);
      })

      .catch((error) => {
        console.log(error);
      });
  };

  // formik params
  // validation rules of the form fields
  validationSchemaEdit = Yup.object().shape({
    subject_name: Yup.string().required("Subject name is required"),
    subject_code: Yup.string().required("Short code is required"),
    subject_type: Yup.string().required("Subject type is required"),
  });
  // submit form
  onSubmitEdit = (values) => {
    let subject_id = this.state.subject_id;
    let short_code = this.state.Edited_ShortCode;
    let created_for = this.state.school_id;
    let subject = this.state.subject_name;
    let subject_type = this.state.subject_type;
    console.log(subject_type);
    let self = this;
    EDU_AXIOS.put(process.env.REACT_APP_API_PATH + "/sm/edit-subject", {
      subject_id,
      created_for,
      short_code,
      subject,
      subject_type,
    })
      .then((res) => {
        self.getSubjects();
        self.setState({
          modalVisibilityEditSubject: false,
        });
        notify("success", res.data.message);
      })
      .catch((error) => {
        console.log(error);
      });
  };

  // to edit subject [Is-Subject-Exist]

  isSubjectExist(event) {
    event.preventDefault();

    let subject = this.state.subject_name;
    let self = this;

    EDU_AXIOS.post(process.env.REACT_APP_API_PATH + "/sm/is-subject-exist", {
      subject,
      created_for: "global",
    })
      .then((res) => {
        self.setState({ subject_name: res.data.subject_name });
        notify("success", res.data.message);
      })
      .catch((error) => {
        console.log(error);
      });
  }

  // to edit subject [Is-Code-Exist]

  isCodeExist(event) {
    event.preventDefault();

    let short_code = this.state.Edited_ShortCode;

    let self = this;

    EDU_AXIOS.post(process.env.REACT_APP_API_PATH + "/sm/is-short-code-exist", {
      short_code,
      created_for: "global",
    })
      .then((res) => {
        self.setState({
          short_code: res.data.Edited_ShortCode,
        });
        notify("success", res.data.message);
      })
      .catch((error) => {
        console.log(error);
      });
  }

  // to block subject only for the school

  handleBlockSubject(event, subject_id) {
    event.preventDefault();
    console.log("Subject-ID-To-Block=", subject_id);
    let self = this;
    EDU_AXIOS.put(
      process.env.REACT_APP_API_PATH + "/sm/block-unblock-subject",
      {
        subject_id: subject_id,
      }
    )
      .then((res) => {
        self.getSubjects();
        notify("success", res.data.message);
      })
      .catch((error) => {
        console.log(error);
      });
  }

  // Delete Subjects
  deleteSubjects(e, subject_id) {
    e.preventDefault();
    console.log("Subject-ID-To-Delete", subject_id);
    let self = this;
    let url =
      process.env.REACT_APP_API_PATH + "/sm/delete-subject/" + subject_id;
    EDU_AXIOS.delete(url)
      .then((resp) => {
        self.getSubjects();
        notify("success", resp.data.message);
      })

      .catch((error) => {
        console.log(error);
      });
  }

  render() {
    let {
      subjects,
      modalVisibilityAddSubject,
      modalVisibilityEditSubject,
      subject_name,
      errorsForAddSubject,
      Edited_ShortCode,
      subject_type,
    } = this.state;

    const columns = [
      {
        title: "Name",
        field: "subject_name",
      },
      {
        title: "Subject Code",
        field: "sort_code",
      },
      {
        title: "Status",
        field: "status",
        render: (data) => {
          if (data.status == "active") {
            return <CBadge color="success">Active</CBadge>;
          }
          if (data.status == "deactivated") {
            return <CBadge color="danger">Inactive</CBadge>;
          }
        },
      },
      {
        title: "Type",
        field: "nature",
      },
    ];
    return (
      <CCard>
        <CCardBody>
          {/*Modal box to Add a Subject*/}
          <CModal
            visible={modalVisibilityAddSubject}
            onDismiss={() =>
              this.setState({ modalVisibilityAddSubject: false })
            }
          >
            <CModalHeader
              onDismiss={() =>
                this.setState({ modalVisibilityAddSubject: false })
              }
            >
              <CModalTitle>
                <h5>Create Subject</h5>
              </CModalTitle>
            </CModalHeader>
            <Formik
              initialValues={this.initialValues}
              validationSchema={this.validationSchema}
              onSubmit={this.onSubmit}
            >
              {(formik) => (
                <Form className="row g-3">
                  <CModalBody>
                    <CRow className="mb-3">
                      <CCol md={12}>
                        <CFormLabel
                          htmlFor="subject_name"
                          className="required-field"
                        >
                          Subject Name
                        </CFormLabel>
                        <EVFormikControl
                          control="input"
                          type="text"
                          name="subject_name"
                          id="subject_name"
                          placeholder="Select name"
                        />
                      </CCol>
                    </CRow>
                    <CRow>
                      <CCol md={12}>
                        <CFormLabel
                          htmlFor="subject_type"
                          className="required-field"
                        >
                          Subject Type
                        </CFormLabel>
                        <EVFormikControl
                          control="selectwithkey"
                          name="subject_type"
                          id="subject_type"
                          placeholder="Select subject type..."
                          options={nature}
                        />
                      </CCol>
                    </CRow>
                  </CModalBody>
                  <CModalFooter>
                    <CButton
                      color="light"
                      onClick={() =>
                        this.setState({ modalVisibilityAddSubject: false })
                      }
                    >
                      <FontAwesomeIcon icon={faTimesCircle} /> Cancel
                    </CButton>
                    <CButton
                      color="dark"
                      type="submit"
                      value="Submit"
                      disabled={
                        formik.isSubmitting || !formik.dirty || !formik.isValid
                      }
                    >
                      {formik.isSubmitting ? (
                        <Loader />
                      ) : (
                        <>
                          <FontAwesomeIcon icon={faSave} /> Save
                        </>
                      )}
                    </CButton>
                  </CModalFooter>
                </Form>
              )}
            </Formik>
          </CModal>
          {/*End modal to add subject*/}

          {/* Start of Edit Subject Modal*/}
          <CModal
            visible={modalVisibilityEditSubject}
            onDismiss={() =>
              this.setState({ modalVisibilityEditSubject: false })
            }
          >
            <CModalHeader
              onDismiss={() =>
                this.setState({ modalVisibilityEditSubject: false })
              }
            >
              <CModalTitle>
                <h5>Edit Subject</h5>
              </CModalTitle>
            </CModalHeader>
            <Formik
              initialValues={{
                subject_name: subject_name,
                subject_code: Edited_ShortCode,
                subject_type: subject_type,
              }}
              validationSchema={this.validationSchemaEdit}
              onSubmit={this.onSubmitEdit}
              enableReinitialize={true}
            >
              {(formik) => (
                <Form className="row g-3">
                  <CModalBody>
                    <CFormLabel
                      htmlFor="subject_name"
                      className="required-field"
                    >
                      Subject Name
                    </CFormLabel>
                    <CInputGroup className="mb-3">
                      <CCol md={11}>
                        <EVFormikControl
                          control="input"
                          type="text"
                          name="subject_name"
                          id="subject_name"
                          value={formik.values.subject_name}
                          onChange={({ target }) => {
                            this.setState({ subject_name: target.value });
                          }}
                          placeholder="Select name"
                        />
                      </CCol>
                      <CCol md={1}>
                        <CButton
                          type="button"
                          color="dark"
                          id="button-addon2"
                          onClick={(e) => this.isSubjectExist(e)}
                          data-toggle="tooltip"
                          data-placement="top"
                          title="Click to check subject name available or not"
                        >
                          <FontAwesomeIcon icon={faQuestion} />
                        </CButton>
                      </CCol>
                    </CInputGroup>
                    <CFormLabel
                      htmlFor="subject_code"
                      className="required-field"
                    >
                      Short Code
                    </CFormLabel>
                    <CInputGroup className="mb-3">
                      <CCol md={11}>
                        <EVFormikControl
                          control="input"
                          type="text"
                          name="subject_code"
                          id="subject_code"
                          disabled="disabled"
                          value={formik.values.subject_code}
                          onChange={({ target }) => {
                            this.setState({ Edited_ShortCode: target.value });
                          }}
                          placeholder="Subject code"
                        />
                      </CCol>
                      <CCol md={1}>
                        <CButton
                          type="button"
                          color="dark"
                          id="button-addon2"
                          onClick={(e) => this.isCodeExist(e)}
                          data-toggle="tooltip"
                          data-placement="top"
                          title="Click to check short code available or not"
                        >
                          <FontAwesomeIcon icon={faQuestion} />
                        </CButton>
                      </CCol>
                    </CInputGroup>
                    <CRow mt={3}>
                      <CCol md={12}>
                        <CFormLabel
                          htmlFor="subject_type"
                          className="required-field"
                        >
                          Subject Type
                        </CFormLabel>

                        <Field name="subject_type">
                          {({ field }) => (
                            <CFormSelect
                              {...field}
                              value={formik.values.subject_type}
                              onChange={({ target }) => {
                                this.setState({ subject_type: target.value });
                              }}
                              className={`form-control ${
                                formik.errors.subject_type &&
                                formik.touched.subject_type
                                  ? "is-invalid"
                                  : ""
                              }`}
                            >
                              <option value="">Select...</option>
                              {nature.map((i) => (
                                <option key={i.value} value={i.value}>
                                  {i.text}
                                </option>
                              ))}
                            </CFormSelect>
                          )}
                        </Field>
                        <ErrorMessage
                          name="subject_type"
                          component="div"
                          className="invalid-feedback"
                        />
                      </CCol>
                    </CRow>
                  </CModalBody>
                  <CModalFooter>
                    <CButton
                      color="light"
                      onClick={() =>
                        this.setState({ modalVisibilityEditSubject: false })
                      }
                    >
                      <FontAwesomeIcon icon={faTimesCircle} /> Cancel
                    </CButton>
                    <CButton
                      color="dark"
                      type="submit"
                      value="Submit"
                      disabled={formik.isSubmitting || !formik.isValid}
                    >
                      {formik.isSubmitting ? (
                        <Loader />
                      ) : (
                        <>
                          <FontAwesomeIcon icon={faSave} /> Save
                        </>
                      )}
                    </CButton>
                  </CModalFooter>
                </Form>
              )}
            </Formik>
          </CModal>
          {/*End modal to Edit subject*/}

          {/*Render data into table*/}
          <MaterialTable
            title="All Subjects"
            data={subjects}
            columns={columns}
            onSelectionChange={(rows) => setSelectedRows(rows)}
            options={{
              search: true,
              paging: true,
              filtering: false,
              exportButton: true,
              actionsColumnIndex: -1,
              rowStyle: (x) => {
                if (x.tableData.id % 2) {
                  return { backgroundColor: "#f9f9f9" };
                }
              },
              pageSize: 15,
              emptyRowsWhenPaging: true,
              pageSizeOptions: [15, 30, 45],
            }}
            actions={[
              {
                icon: () => (
                  <CButton
                    color="dark"
                    onClick={() =>
                      this.setState({ modalVisibilityAddSubject: true })
                    }
                  >
                    {/* Action For Add Subject */}
                    <FontAwesomeIcon icon={faPlusCircle} /> Add
                  </CButton>
                ),
                tooltip: "Add Subject",
                isFreeAction: true,
              },

              // {/* Action For Edit Subjects */}
              (data) => ({
                icon: () => (
                  <FontAwesomeIcon
                    icon={faEdit}
                    style={{ fontSize: "1rem" }}
                    onClick={() =>
                      this.setState({
                        modalVisibilityEditSubject: true,
                        Edited_ShortCode: data.sort_code,
                        subject_name: data.subject_name,
                        subject_id: data.subject_id,
                        subject_type: data.nature,
                      })
                    }
                  />
                ),
                tooltip: "Edit Subject",
                onClick: "",
              }),

              // {/* Action For Delete Subjects */}
              (data) => ({
                icon: () => (
                  <FontAwesomeIcon
                    icon={faTrash}
                    style={{ fontSize: "1rem" }}
                    onClick={(e) => this.deleteSubjects(e, data.subject_id)}
                  />
                ),
                tooltip: "Delete Subject",
                onClick: "",
                disabled: data.status === "active" ? false : true,
              }),
            ]}
          />
          {/*End table*/}
        </CCardBody>
      </CCard>
    );
  }
}

export default all_subjects;
